body {
  background: radial-gradient(circle, rgba(238,174,174,1) 0%, rgba(148,148,233,1) 100%);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  clip: rect(1px, 1px, 1px, 1px);
}

.container {
  position: relative;
  width: 900px;
  min-height: 550px;
  margin-top: 50px;
  margin-left: 50px;
  border: 1px solid #e2dada;
  padding: 25px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 18px;
  font-weight: lighter;
  letter-spacing: 1;
  color: #5a5959;
}

.header {
  display: flex;
  justify-content: flex-end;
}

.header-button {
  display: inline-block;
  box-sizing: border-box;
  width: 200px;
  height: 45px;
  margin-bottom: 20px;
  padding: 10px;
  border: 1px solid #5a5959;
  border-radius: 15px;
  background-color: #ffffff;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  letter-spacing: inherit;
  color: inherit;
  text-align: center;
  cursor: pointer;
}

.tickets {
  display: block;
}

.tickets-list {
  display: block;
}

tr {
  display: flex;
  background-color: #ffffff;
  border: 1px solid #5a5959;
  border-bottom: none;
  padding: 10px;
}

tr:last-child {
  border-bottom: 1px solid #5a5959;
}

td {
  box-sizing: border-box;
  padding: 5px;
}

.ticket-status {
  width: 5%;
}

.ticket-content {
  width: 70%;

}

.ticket-data {
  width: 15%;

}

.ticket-actions {
  width: 10%
}

.done, .todo, .edit-button, .delete-button {
  display: inline-block;
  width: 25px;
  height: 25px;
  background-size: 15px;
  background-repeat: no-repeat;
  background-position: center center;
  border: 1px solid #5a5959;
  border-radius: 50px;
  cursor: pointer;
}

.done {
  background-image: url(checkmark.png);
}

.ticked-description {
  margin-top: 15px;
}

.ticket-data {
  align-self: flex-start;
}

.ticket-actions {
  text-align: right;
}

.edit-button {
  background-image: url(edit.png);
}

.delete-button {
  background-image: url(delete.png);
}

.edit, .modal {
  display: none;
  position: absolute;
  width: 50%;
  background-color: #ffffff;
  padding: 20px;
  border: 1px solid #5a5959;
  border-radius: 15px;
  margin-top: 10px;
}

.edit-active, .modal-active {
  display: block;
}

.form-header, .modal-header {
  text-align: center;
}

p {
  padding: 0;
  margin: 15px 0 10px;
}

.edit-input {
  display: block;
  box-sizing: border-box;
  width: 100%;
  padding: 10px;
  border: 1px solid #5a5959;
}

.edit-input:focus {
  outline: none;
  border: 2px solid #5a5959;
}

textarea {
  height: 90px;
	resize: none;
}

.buttons-box {
  display: flex;
  justify-content: flex-end;
}

.button {
  display: inline-block;
  box-sizing: border-box;
  padding: 10px;
  border: 1px solid #5a5959;
  border-radius: 15px;
  margin: 20px 10px 0;
  background-color: #ffffff;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  letter-spacing: inherit;
  color: inherit;
  cursor: pointer;
}

.cancel-button {
  width: 35%;
}

.submit-button {
  width: 15%;
}

.modal-content {
  display: inline-block;
  margin-top: 15px;
}
